@import url(https://fonts.googleapis.com/css?family=Press+Start+2P);
.block {
  box-shadow: inset 0px 0px 0px 4px rgba(0, 0, 0, 0.25);
  border: solid 1px var(--stack-bg-color);
}

.block.void {
  background: var(--app-bg-color);
  box-shadow: inset 0px 0px 0px 4px rgba(0, 0, 0, 0.75);
  /*visibility: hidden;*/
}

.block.I {
  background: AquaMarine;
}

.block.J {
  background: DodgerBlue;
}

.block.L {
  background: DarkOrange;
}

.block.O {
  background: Yellow;
}

.block.S {
  background: LimeGreen;
}

.block.T {
  background: DarkMagenta;
}

.block.Z {
  background: OrangeRed;
}

.stack {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  background: var(--stack-bg-color);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.stack > .row {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.stack > .row.animateRowDrop {
  -webkit-filter: saturate(0.3) brightness(10);
          filter: saturate(0.3) brightness(10);
  -webkit-transition: -webkit-filter ease-in 0.4s;
